body * {
  box-sizing: border-box;
}

h1, h2, h3, h4, p, div, body, a, button, ul, li {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  width: 280px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (width >= 768px) and (width <= 1199px) {
  .container {
    width: 700px;
  }
}

@media (width >= 1199px) {
  .container {
    width: 1119px;
  }
}

.header {
  background-color: #ffb8ca;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  display: flex;
}

.header-list {
  gap: 30px;
  display: flex;
}

.header-nav {
  align-items: center;
  gap: 30px;
  display: flex;
}

.header-link {
  color: #fff;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  list-style: none;
}

.header-btn {
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  padding: 7px 36px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  display: flex;
}
